<template>
	<div class="account">
		<v-container class="my-1">
			<h1>This is the account page</h1>
			<v-btn @click="updateUser">Update user details</v-btn>
			<v-btn @click="updateItemsOut">Update items out</v-btn>
			<v-btn @click="updateItemsOnHold">Update items on hold</v-btn>

			<h2>Overdue</h2>
			<v-card>
				<v-list>
					<v-list-tile v-for="item in overdueItems" :key="item.id" class="error" ripple @click.stop>
						<v-list-tile-content>
							<v-list-tile-title>{{ item.title }} <span v-if="item.author"> by {{ item.author }}</span></v-list-tile-title>
							<v-list-tile-sub-title class="">Due back {{ daysDue( item.dueDate ) }} - {{ item.barcode }}</v-list-tile-sub-title>
						</v-list-tile-content>
					</v-list-tile>
				</v-list>
			</v-card>


			<router-view></router-view>
		</v-container>
	</div>
</template>

<script>
var moment = require( 'moment' );

export default {
	name: 'Account',
	data ( ) {
		return {
		}
	},
	computed: {
		overdueItems( ) {
			return this.$store.state.patron.itemsOut.data.filter( item => {
				return item.overdue
			} );
		}
	},
	methods: {
		updateUser( ) {
			this.$store.dispatch( 'patron/updatePatron' );
		},
		updateItemsOut( ) {
			this.$store.dispatch( 'patron/updateItemsOut' );
		},
		updateItemsOnHold( ) {
			this.$store.dispatch( 'patron/updateItemsOnHold' );
		},
		daysDue( date ) {
			return moment( date ).fromNow( );
		}
	}
}</script>

